.Home {
  font-family: "Mark Pro", sans-serif;
}

.Home .navbar {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 80px;
  left: 0px;
  top: 0px;
  /* white */
  background: #ffffff;
  /* primary 1 */
  border-bottom: 1px solid #d9121b;
}

.Home .navbar .logo {
  position: absolute;
  width: 125px;
  height: 40px;
  left: 80px;
  top: 20px;
}

.Home .navbar .top {
  display: flex;
  flex-direction: row;
  list-style-type: none;
}

.Home .navbar .top img {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.Home .navbar .top .home {
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 82px;
  height: 16px;
  left: 1102px;
  top: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  /* primary 1 */
  color: #d9121b;
}

.Home .navbar .top .notifications {
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 82px;
  height: 16px;
  left: 1242px;
  top: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  /* secondary 4 */
  color: #9c9d9f;
}

.Home .navbar .top .notifications img {
  position: absolute;
}

.Home .navbar .top .notifications span {
  font-weight: bold;
  margin-left: 40px;
}

.Home .navbar .top .notifications #dot {
  height: 8px;
  width: 8px;
  margin-left: 15px;
}

.Home .navbar .top .users {
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 88px;
  height: 16px;
  left: 1382px;
  top: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  /* secondary 4 */
  color: #9c9d9f;
}

.Home .navbar .top .media {
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 88px;
  height: 16px;
  left: 1522px;
  top: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  /* secondary 4 */
  color: #9c9d9f;
}

.Home .header {
  position: absolute;
  width: 174px;
  height: 25px;
  left: 180px;
  top: 104px;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  /* primary 1 */
  color: #d9121b;
}

.Home button {
  margin-top: 50px;
  margin-left: 50px;
}

hr {
  width: 100%;
